@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
    display: flex;
}

.Sidebar {
  height: 100vh;
  max-width: 350px;
  min-width: 260px;
  background-color: #00aec8;
  border-right: 2px solid #043ebb;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: column;
}

.Sidebar > .Sidebar-header {
  margin: 1.5rem;
  font-family: "Press Start 2P", "Roboto", cursive;
  margin-bottom: 1rem;
}

.Sidebar > .Sidebar-header > h1 {
  color: #f8ec72;
  font-weight: 100;
}

.Sidebar > .Sidebar-header > h2 {
  margin: 2rem 0;
  color: #f7f2ba;
  font-size: 0.8rem;
  font-weight: 100;
  transform: scale(1, 1.8);
}

.Sidebar > .Sidebar-inputs {
  font-size: 1rem;
  margin: 1.5rem;
}

.Sidebar > .Sidebar-inputs input {
  width: 100%;
  margin-bottom: 0.3rem;
  border: none;
  height: 2rem;
  border-left: 3px solid #57b519;
  padding-left: 0.8rem;
  font-size: 1rem;
}

.Sidebar > .Sidebar-inputs input:focus {
  border-left: 3px solid #f58113;
}

.Sidebar > .Sidebar-inputs button {
  font-size: 1rem;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  background-color: #57b519;
  border: none;
  box-shadow: 3px 3px 6px #00000076;
  color: white;
  margin-bottom: 1rem;
  cursor: pointer;
}

.Sidebar > .Sidebar-inputs button:hover {
  background-color: #65be2a;
}

.Sidebar > .Sidebar-inputs button:active {
  transform: translate(2px, 2px);
}

.Sidebar > .Sidebar-inputs button:focus {
  outline: 0;
}

.Sidebar > .Sidebar-credits {
  background-color: #00000044;
  width: 100%;
  font-family: "Press Start 2P", "Roboto", cursive;
  font-size: 0.6rem;
  font-weight: 100;
  color: #f7f2ba;
  margin-top: auto;
  display: inline-flex;
  justify-content: space-around;
}

.Sidebar > .Sidebar-credits > span {
  text-align: center;
  padding: 1rem 0.5rem;
  line-height: 0.7rem;
}

.Sidebar > .Sidebar-random {
  font-size: 1rem;
  padding: 0.4rem 1rem;
  margin: 1rem 1.8rem;
  border-radius: 20px;
  background-color: #f58113;
  border: none;
  box-shadow: 3px 3px 6px #00000076;
  color: white;
  cursor: pointer;
  margin-bottom: 1rem;
}

.Sidebar > .Sidebar-random:hover {
  background-color: #f89233;
}

.Sidebar > .Sidebar-random:active {
  transform: translate(2px, 2px);
}

.Sidebar > .Sidebar-random:focus {
  outline: 0;
}

.CardsContainer {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 300px));
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-evenly;

    padding: 2rem;
}

.Card {
  background-color: #3b3b3b;
  overflow: hidden;
  border-radius: 10px;
  border: 1px solid #707070;
  max-height: 450px;
  box-shadow: 3px 3px 15px #01010199;
  color: white;
}

.Card > .Card-image {
  position: relative;
  overflow: hidden;
  height: 200px;
  background-position: center;
  background-size: cover;
}

.Card > .Card-image > h3 {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  text-shadow: 2px 2px 10px #000000, 0px 0px 3px #000000;
}

.Card > .Card-id {
  border-bottom: 1px solid #707070;
  border-top: 1px solid #707070;
  border-radius: 0 0 8px 8px;
  background-color: #919191;
  padding: 0.2rem;
  padding-left: 1rem;
  font-weight: bold;
  color: #f3f3f3;
  font-size: 0.9rem;
}

.Card > .Card-status {
  padding: 0.5rem;
  font-size: 1rem;
}

.Card > .Card-status ul {
  list-style: none;
}

.Card > .Card-status li {
  text-align: center;
  margin: 0.5rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #b1b1b144;
}

.Card > .Card-status li > .Card-status-data {
  text-align: right;
}

.Card > .Card-status li > .Card-status-position {
  color: #b6b6b6;
}

.Card > .Card-status li:last-child {
  border: none;
}

